@import url(https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
/* base styles */
body {
  font-family: 'DM Sans', sans-serif;
  margin: 0 0 50px 0;
  font-size: 1.1em;

}
ul, li, p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}
.btn {
  background: none;
  border: 2px solid #1f9751;
  padding: 6px 12px;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
.btn:hover {
  background: #1f9751;
  color: #fff;
}

.section1{
  background-image: url(/static/media/homebg.a0cdbcd2.svg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  width: 100%;
  height: 550px;
}

.section1 h1{
  color: #FFFFFF;
  width: 490px;
  padding-left: 77px;
  margin: auto 0;
  top: 247px;
  font-style: normal;
  font-weight: 700;
  font-size: 72px;
  line-height: 94px;
  letter-spacing: -0.05em;
}
.search-span{
  display: block;
}
.search{
  width:  calc(100% - 154px);
  color: #000;
  margin: 50px auto 0 auto;
}
.search-label input{
  width: 100%;
  border: 1px solid #000;
  padding: 10px 2px;
}
.movies{
  width:  calc(100% - 77px);
  margin-left: auto;
  margin-top: 50px;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.clip::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.clip {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}
.clip{
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
}

.clip div{
  height: 300px;
  width: 300px;
  border-radius: 12px;
  background: #000;
  flex-shrink: 0;
  position: relative;
  text-align: center;
  overflow: hidden;
}
.movie-title{
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  text-align: center;
  width: 80%;
  font-weight: 400;
  font-size: 24px;

}
.clip-image{
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.error, .loading{
  width: 100%;
  text-align: center;
  margin-top: 10px;
}

@media (max-width: 834px) {
  .section1{
    justify-content: center;
  }
  .section1 h1{
    padding-left: 0;
    text-align: center;
  }
}
@media (max-width: 320px) {
  .section1{
    height: 257px;
  }
  .section1 h1{
    font-size: 28px;
    line-height: 36px;

  }
  .search{
    width:  calc(100% - 56px);
  }
  .movies{
    width:  calc(100% - 28px);
  }
  .clip div{
    width: 200px;
    height: 200px;
  }
}
/* navbar styles */
nav{
  background: #292929;
  padding-top: 30px;
  padding-bottom: 30px;
}

nav img{
  width: 192px;
  padding-left: 77px;
}
@media (max-width: 834px) {
  nav{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  nav img{
    padding-left: 0;
  }
}
@media (max-width: 320px) {
  nav{
    padding-top: 16px;
    padding-bottom: 16px;
  }
  nav img{
    width: 87.68px;
    height: 18.43px;
  }
}
